.header {
    height:110px;
    width: 100%;
    background-color: white;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    position: fixed;
    &_logo {
        width: 0%;
        height: 100%;
        background-color: white;
        transition: 2s;
            &:hover{
                 width: 55%;
                 background-color: whitesmoke;
                 transform: translateX(10%);              
             }
    }
    &_nav {
        width: 60%;
        background-color: white;
        transition: 2s;
    }
    &_menu {
        display: flex;
        height: 80%;
        width: 100%;
        justify-content: space-evenly;
        list-style-type: none;
        text-decoration: none;
        margin-top: 0px;
    }
    &_element {
        height:100%;
        width: 20%;
        border-bottom: 4px solid transparent;
        transition: 2s;
        text-decoration: none;
             &:hover {
                border-color: #0470b0;

             } 
    }
    &_link {
        color: grey;
        text-align: center;
        margin-top: 31px;
        font-family: 'Open Sans', sans-serif;
        text-transform: uppercase;
        font-weight: 500;
        letter-spacing: 4px;
        transition: 1s;
            &:hover{
                color: black;
            }

    }

};


.main {
    height: 100vh;
}

.bud {
    display: none;
}



